body {
  font-family: BCSans, "Noto Sans", Verdana, Arial, sans-serif;
  color: #313132;
}

.bcgov-header-group {
  border-bottom: 2px solid;
}

.wp-site-blocks .site-banner {
  margin-top: 0;
}

.wp-block-template-part.site-header .bcgov-header-group {
  border-top: 0;
  border-left: 0;
  border-right: 0;
}
.wp-block-template-part.site-header nav.wp-block-navigation button {
  background: svg(url(../../../assets/images/bcgov-hamburger.svg));
  flex: 1 auto;
  flex-direction: row-reverse;
}
.wp-block-template-part.site-header .wp-block-search .wp-block-search__inside-wrapper {
  border: none;
}
.wp-block-template-part.site-header .wp-block-search .wp-block-search__inside-wrapper button {
  margin-left: 1px;
}